import test from 'ava';

var pgtest = require('pgtest');
var rewire = require("rewire");
var myModule = rewire("../models/contact.js");

pgtest.expect('SELECT firstname, lastname FROM salesforce.contact').returning(null,[[ 'John', 'Rambo' ],[ 'Clark', 'Kent' ]]);

var pgMock = {
    connect : function(url,callback){
        return '{}';
    }
};


myModule.__set__("pg", pgMock);

test('get contacts', async t => {
  t.is(myModule.getContacts(null,null,null),'{}');
});